// -------------------------------------------------------------------------------
//
// Description: Alert, warning
// ===============================================================================
//

.buf-alert--warning {
  color: $buf-color-alert-warning-text;
  background-color: $buf-color-alert-warning-bg;

  a {
    @include buf-text-underline($buf-color-alert-warning-text, darken($buf-color-alert-warning-text, 20%), $buf-color-alert-warning-text);
  }

  .buf-alert__icon {
    &::after {
      background-color: darken($buf-color-alert-warning-bg, 30%);
    }
  }
}
