// -------------------------------------------------------------------------------
//
// Description: Functionality for retrieving theme color codes and organizing
// themes using multidimensional map.
// ===============================================================================
//

// Need to import colors to use global available colors
@import './../../03-base/colors';

// Theme map
$buf-cta-themes: (
  default: (
    link: (
      active: $buf-color-action,
      hover: $buf-color-action-hover
    ),

    linkBorder: (
      active: 1px solid $buf-color-action,
      hover: transparent
    ),

    buttonColor: (
      active: $color-white,
      hover: $color-white
    ),

    buttonColorSecondary: (
      active: $buf-color-action,
      hover: $color-white
    ),

    buttonBackground: (
      active: $buf-color-action,
      hover: $buf-color-action-hover
    ),

    buttonBorder: (
      active: $buf-color-action,
      hover: $buf-color-action-hover
    )
  ),

  bufetat: (
    link: (
      active: $buf-color-action-bufetat,
      hover: $buf-color-action-hover-bufetat
    ),

    linkBorder: (
      active: 1px solid $buf-color-action-bufetat,
      hover: transparent
    ),

    buttonColor: (
      active: $color-white,
      hover: $color-white
    ),

    buttonColorSecondary: (
      active: $buf-color-action-bufetat,
      hover: $color-white
    ),

    buttonBackground: (
      active: $buf-color-action-bufetat,
      hover: $buf-color-action-hover-bufetat
    ),

    buttonBorder: (
      active: $buf-color-action-bufetat,
      hover: $buf-color-action-hover-bufetat
    )
  ),
);

// Text links
// -------------------------------------------------------------------------------
@mixin buf-link-theme($theme: 'default') {
  color: buf-cta($theme, 'link');
  border-bottom: buf-cta($theme, 'linkBorder');

  &:hover, &:focus {
    color: buf-cta-hover($theme, 'link');
    border-bottom-color: buf-cta-hover($theme, 'linkBorder');
  }
}

// Button, filled
// -------------------------------------------------------------------------------
@mixin buf-button-filled-theme($theme: 'default') {
  color: buf-cta($theme, 'buttonColor');
  background-color: buf-cta($theme, 'buttonBackground');

  &:hover, &:focus {
    color: buf-cta-hover($theme, 'buttonColor');
    background-color: buf-cta-hover($theme, 'buttonBackground');
  }
}

// Button, outline
// -------------------------------------------------------------------------------
@mixin buf-button-outline-theme($theme: 'default') {
  color: buf-cta($theme, 'buttonColorSecondary');
  border-color: buf-cta($theme, 'buttonBorder');
  background-color: $color-white;

  &:hover, &:focus {
    color: buf-cta-hover($theme, 'buttonColorSecondary');
    border-color: buf-cta-hover($theme, 'buttonBorder');
    background-color: buf-cta-hover($theme, 'buttonBackground');
  }
}

// Button, flat
// -------------------------------------------------------------------------------
@mixin buf-button-flat-theme($theme: 'default') {
  color: buf-cta($theme, 'link');
  border-bottom: buf-cta($theme, 'linkBorder');
  background-color: $color-white;

  &:hover, &:focus {
    color: buf-cta-hover($theme, 'link');
    border-bottom-color: buf-cta-hover($theme, 'linkBorder');
    background-color: $color-white;
  }
}
