// -------------------------------------------------------------------------------
//
// Description: Alert, success
// ===============================================================================
//

.buf-alert--success {
  color: $buf-color-alert-success-text;
  background-color: $buf-color-alert-success-bg;

  a {
    @include buf-text-underline($buf-color-alert-success-text, darken($buf-color-alert-success-text, 20%), $buf-color-alert-success-text);
  }

  .buf-alert__icon {
    &::after {
      background-color: darken($buf-color-alert-success-bg, 25%);
    }
  }
}
