// -------------------------------------------------------------------------------
//
// Description: Alert
// ===============================================================================
//

.buf-alert {
  color: $buf-color-alert-default-text;
  display: flex;
  flex-direction: column;
  background-color: $buf-color-alert-default-bg;

  @include mq($from: sm) {
    flex-direction: row;
  }

  a {
    @include buf-text-underline($buf-color-alert-default-text, $buf-color-alert-default-text, $buf-color-alert-default-text);
  }
}

.buf-alert__content {
  @include pr(4);
  @include pb(6);
  @include pl(4);

  @include mq($from: sm) {
    @include p(8);
  }

  &.buf-alert__content--small {
    @include p(4);
  }
}

.buf-alert__title {
  @include m(0);
  @include mb(2);
}

.buf-alert__text {
  @include buf-type-scale(medium);

  &.buf-alert__text--small {
    @include buf-type-scale(base);
  }
}

.buf-alert__icon {
  font-size: rem(40px);
  display: flex;
  justify-content: center;
  position: relative;

  @include pt(2);
  @include pr(0);
  @include pb(2);
  @include pl(0);
  @include mb(2);

  @include mq($from: sm) {
    font-size: rem(72px);
    flex: 0 0 140px;
    align-items: center;

    @include p(0);
    @include m(0);
  }

  &::after {
    width: auto;
    height: 1px;
    content: ' ';
    position: absolute;
    right: $buf-base-unit * 4; bottom: 0; left: $buf-base-unit * 4;
    background-color: darken($buf-color-alert-default-bg, 15%);

    @include mq($from: sm) {
      width: 1px;
      height: auto;
      top: $buf-base-unit * 6; right: 0; bottom: $buf-base-unit * 6; left: auto;
    }
  }
}

// Import alert style
@import './alert-error';
@import './alert-info';
@import './alert-success';
@import './alert-warning';
