// -------------------------------------------------------------------------------
//
// Description: Flat style button
// ===============================================================================
//

.buf-btn--flat {
  border-right: none;
  border-left: none;

  @include p(0);
  @include buf-button-flat-theme('default');

  .buf-bufetat & {
    @include buf-button-flat-theme('bufetat');
  }

  &.buf-btn--accent {
    color: $buf-color-accent;
    border-bottom-color: $buf-color-accent;
  
    &:hover, &:focus {
      color: $buf-color-accent-hover;
      border-bottom-color: $buf-color-accent-hover;
    }
  }
}

.buf-btn--flat:disabled,
.buf-btn--flat[disabled] {
  color: $buf-color-btn-is-disabled-text;
  border-bottom-color: $buf-color-btn-is-disabled-bg;

  &:hover, &:focus {
    color: $buf-color-btn-is-disabled-text;
    border-bottom-color: $buf-color-btn-is-disabled-bg;
    background-color: transparent;
  }
}
