// -------------------------------------------------------------------------------
//
// Description: Filled style button
// ===============================================================================
//

.buf-btn--filled {
  @include buf-button-filled-theme('default');

  .buf-bufetat & {
    @include buf-button-filled-theme('bufetat');
  }

  &.buf-btn--accent {
    background-color: $buf-color-accent;
  
    &:hover, &:focus {
      background-color: $buf-color-accent-hover;
    }
  }
}

.buf-btn--filled:disabled,
.buf-btn--filled[disabled] {
  color: $buf-color-btn-is-disabled-text;
  border: none;
  background-color: $buf-color-btn-is-disabled-bg;

  &:hover, &:focus {
    color: $buf-color-btn-is-disabled-text;
    background-color: $buf-color-btn-is-disabled-bg;
  }
}
