// -------------------------------------------------------------------------------
//
// Description: Button
// ===============================================================================
//

.buf-btn {
  height: 0%;
  text-align: center;
  text-decoration: none;
  display: inline-flex;
  vertical-align: middle;
  border-width: em(2px);
  border-style: solid;
  border-color: transparent;
  cursor: pointer;

  @include pt(4);
  @include pb(4);
  @include pl(6);
  @include pr(6);
  @include buf-transition(all, $transition-default);

  .buf-btn__icon {
    position: relative;
  
    @include buf-transition(right, $transition-default);
  
    &-left {
      @include mr(3);
    }
  
    &-right {
      right: 0;

      @include ml(3);
    }
  }

  &:hover, &:focus {
    .buf-btn__icon {
      &-right {
        right: -5px;
      }
    }
  }
}

.buf-btn__content {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 1 auto;
}

.buf-btn__text {
  white-space: nowrap;
}

.buf-btn:disabled,
.buf-btn[disabled] {
  color: $buf-color-btn-is-disabled-text;
  cursor: no-drop;

  .buf-btn__icon {
    &-right {
      transition: none;
    }
  }

  &:hover, &:focus {
    .buf-btn__icon {
      right: 0;
      transition: none;
    }
  }
}

// Import button style
@import './btn-filled';
@import './btn-flat';
@import './btn-outline';

// Import button size
@import './btn-small';
@import './btn-regular';
@import './btn-large';
