// -------------------------------------------------------------------------------
//
// Description: Alert, error
// ===============================================================================
//

.buf-alert--error {
  color: $buf-color-alert-error-text;
  background-color: $buf-color-alert-error-bg;

  a {
    @include buf-text-underline($buf-color-alert-error-text, darken($buf-color-alert-error-text, 20%), $buf-color-alert-error-text);
  }

  .buf-alert__icon {
    &::after {
      background-color: darken($buf-color-alert-error-bg, 15%);
    }
  }
}
