// -------------------------------------------------------------------------------
//
// Description: Transition mixin, see conf-transition for transition speed
// ===============================================================================
//

@mixin buf-transition($transition-type, $transition-default) {
  transition: $transition-type $transition-default ease-in-out;
}

// Example usage
// -------------------------------------------------------------------------------
// .class-name {
//   @include buf-transition(all, $transition-default);
// }
