// -------------------------------------------------------------------------------
//
// Description: Convert px to em
// ===============================================================================
//

@function em($pixels, $context: $buf-base-font-size) {
  @if (unitless($pixels)) {
    $pixels: $pixels * 1px;
  }
  
  @if (unitless($context)) {
    $context: $context * 1px;
  }
  
  @return $pixels / $context * 1em;
}

// Example usage
// -------------------------------------------------------------------------------
// .class-name {
//   padding: em(16px); // converts to: padding: 1em;
// }
