// -------------------------------------------------------------------------------
//
// Description: Outline style button
// ===============================================================================
//

.buf-btn--outline {
  @include buf-button-outline-theme('default');

  .buf-bufetat & {
    @include buf-button-outline-theme('bufetat');
  }

  &.buf-btn--accent {
    color: $buf-color-accent;
    border-color: $buf-color-accent;
  
    &:hover, &:focus {
      color: $color-white;
      border-color: $buf-color-accent-hover;
      background-color: $buf-color-accent-hover;
    }
  }
}

.buf-btn--outline:disabled,
.buf-btn--outline[disabled] {
  color: $buf-color-btn-is-disabled-text;
  border-color: $buf-color-btn-is-disabled-bg;
  background-color: $color-white;

  &:hover, &:focus {
    color: $buf-color-btn-is-disabled-text;
    border-color: $buf-color-btn-is-disabled-bg;
    background-color: $color-white;
  }
}
