// -------------------------------------------------------------------------------
//
// Description: Alert, info
// ===============================================================================
//

.buf-alert--info {
  color: $buf-color-alert-info-text;
  background-color: $buf-color-alert-info-bg;

  a {
    @include buf-text-underline($buf-color-alert-info-text, darken($buf-color-alert-info-text, 20%), $buf-color-alert-info-text);
  }

  .buf-alert__icon {
    &::after {
      background-color: darken($buf-color-alert-info-bg, 20%);
    }
  }
}
